<template>
  <component
    class="button"
    :is="href ? 'a' : 'button'"
    :href="href"
    v-bind="buttonProps"
  >
    <slot></slot>
  </component>
</template>

<script>
export default {
  name: "BaseButton",
  data(){
    return {
      active: false,
    }
  },
  props: {
    href: {
      required: false,
      type: String,
      default: null
    },
    type: {
      type: String,
      default: "button" //button, submit
    },
    is: {
      type: String,
      defalut: false,
    },
  },

  computed: {
    buttonProps() {
      if (this.isButtonType) {
        return { type: this.type };
      } else {
        return {};
      }
    },
    isButtonType() {
      return !this.href && this.type;
    }
  },
  methods: {
    handleClick(evt) {
      this.$emit("click", evt);
    },
  }
};
</script>
